.comments__edit {
  position: absolute;
  right: 0;
  top: 10%;
  cursor: pointer;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.comment__edit-buttons {
  position: absolute;
  right: 40px;
  top: -10px;
  z-index: 999999;

  > button {
    top: 20px;
    left: 10px;
    text-decoration: none;
  }
}
.no__comments {
  width: 70%;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 200px;
}
.zap-text-copy{
  position: absolute;
  right: 40px;
  cursor: pointer!important;
  svg {
    fill: rgb(24, 52, 56);
  }
}
.date_create-update{
  display: flex;
  flex-direction: column;
font-style: italic;
text-align: left;
flex-grow: unset;
gap: 5px;

  .created_at{
    color: rgb(34, 33, 33);
  }
  .updated_at{
    color: rgb(6, 109, 2);
    margin-bottom: 5px;
  }
svg {
  width: 20px;
  height: 20px;
}
}
@media screen and (max-width: 600px) {  

  .zap__comments-show {
    position: fixed;
    width: 90%;
    height: 100vh;
  }
  .disabled__comments {
    svg {
      width: 70px;
    }
  }
  .comments__item-block{
    height: 50vh;
  }
}